<template>
    <v-container class="d-flex justify-space-between flex-column flex-grow-1">
        <div class="mt-n4">
            <h6 class="text-h6 pb-4">M kha trnh np khi ng</h6>

            <div class="text-body-1">
                <p>
                     ci t ROM khc ln thit b ca bn, bn cn m
