                </v-card-title>

                <v-card-text>
                    <p>
                        V l do bo mt, vic m kha trnh khi ng b v hiu ha
                        theo mc nh. Bn cn kch hot M kha OEM bng cc bc sau:
                    </p>

                    <ol class="ml-4 mb-4">
                        <li>Khi ng li vo h thng Android</li>
                        <li>
                            Vo Ci t  Gii thiu v in thoi v lt xung di cng
                        </li>
                        <li>
                            Chm S bn dng lin tc cho n khi Ty chn nh pht trin 
                            c kch hot
                        </li>
                        <li>
                            Vo Ci t  H thng  Ty chn nh pht trin
                        </li>
